@import '../../styles/variables.scss';

.accordion {
  border-radius: 0.4rem;
  overflow: hidden;
  box-shadow: 0px 0px 5px 0px rgba($gray, 0.75);
  background-color: $white;
}

.listItem {
  border-bottom: 1px solid rgba($gray, 0.4);
  padding: 10px 15px;

  &:last-child {
    border: none;
  }
}
